.main_banner {
  height: 7rem;
  overflow: hidden;
  position: relative;
  border-radius: 0.5rem;
  display: flex;
  align-items: center;
}
.main_banner-filter {
  position: absolute;
  z-index: -1;
  height: 100%;
  width: 100%;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.25) 0%, rgba(0, 0, 0, 0.25) 100%);
}
.main_banner img {
  -o-object-fit: cover;
     object-fit: cover;
  position: absolute;
  top: -50%;
  width: 100%;
  z-index: -2;
}
.main_banner h1 {
  color: white;
  font-weight: 500;
  font-size: 1.5rem;
  letter-spacing: 0.1rem;
  margin-left: 1rem;
}/*# sourceMappingURL=banner.css.map */